export const weeklyRules = {
  counterSaleAmts: {
    alias: '店面销售额',
    required: true,
    pattern: /^([5-9]\d{1})|([1-9]\d{2,})$/,
    message: '周报-店面销售额必须>=50'
  },
  lijiaSalesAmts: {
    alias: '理家课堂销售额',
    required: true,
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '周报-理家课堂销售额必须>=0'
  },
  grpbSalesAmts: {
    alias: '团购销售额',
    validate (val, target) {
      return val > 20000 || val === 0 || val === '0' || '周报-团购销售额必须大于2万，才属于团购业绩，否则请修改为0'
    }
  },
  customisedSalesAmts: {
    alias: '私人订制销售额',
    required: true,
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '周报-私人订制销售额必须>=0'
  },
  onlinesaleamt: {
    alias: '线上销售额（含eTUP）',
    required: true,
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '周报-线上销售额（含eTUP）必须>=0'
  },
  dealAmts: {
    alias: '成交笔数',
    required: true,
    pattern: /^[+]?\d+$/,
    message: '周报-成交笔数必须为>=0的整数'
  },
  naturalCustomerNo: {
    alias: '自然入店顾客',
    required: true,
    pattern: /^[+]?\d+$/,
    message: '周报-自然入店顾客必须为>=0的整数'
  },
  comebackCustomerNo: {
    alias: '回店会员数',
    required: true,
    pattern: /^[+]?\d+$/,
    message: '周报-回店会员数必须为>=0的整数'
  },
  customisedCustomerNo: {
    alias: '私人订制顾客数',
    required: true,
    pattern: /^[+]?\d+$/,
    message: '周报-私人订制顾客数必须为>=0的整数'
  },
  lijiaCustomerNo: {
    alias: '参加理家课堂顾客数',
    required: true,
    pattern: /^[+]?\d+$/,
    message: '周报-参加理家课堂顾客数必须为>=0的整数'
  },
  lijiaPrsttTimes: {
    alias: '理家课堂次数',
    required: true,
    pattern: /^([0-9]|[1-4][0-9])$/,
    message: '周报-理家课堂次数必须为>=0且<50的整数'
  },
  imdtlyPrsttTimes: {
    alias: '即时示范数',
    required: true,
    pattern: /^[+]?\d+$/,
    message: '周报-即时示范数必须为>=0的整数'
  },
  waterPurifierSalesAmts: {
    alias: '净水器销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9])$/,
    message: '周报-净水器销售数量必须为>=0且<100的整数'
  },
  wqpanSaleNo: {
    alias: '5.7L锅销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9])$/,
    message: '周报-5.7L锅销售数量必须为>=0且<100的整数'
  },
  cupSaleNo: {
    alias: '水杯销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9]|[1-4][0-9][0-9])$/,
    message: '周报-水杯销售数量必须为>=0且<500的整数'
  },
  slimBeautySetNo: {
    alias: '纤巧超值套装销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9])$/,
    message: '周报-纤巧超值套装销售数量必须为>=0且<100的整数'
  },
  slevelPanSaleNo: {
    alias: '三层炒锅销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9])$/,
    message: '周报-三层炒锅销售数量必须为>=0且<100的整数'
  },
  thermosSaleNo: {
    alias: '保温杯销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9]|[1-4][0-9][0-9])$/,
    message: '周报-保温杯销售数量必须为>=0且<500的整数'
  },
  sskitSaleNo: {
    alias: '43件套销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9])$/,
    message: '周报-43件套销售数量必须为>=0且<100的整数'
  },
  zzylgNo: {
    alias: '至尊压力锅销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9])$/,
    message: '周报-至尊压力锅销售数量必须为>=0且<100的整数'
  },
  xyjydbNo: {
    alias: '鳕鱼胶原蛋白肽饮品销售数量',
    required: true,
    pattern: /^([0-9]|[1-9][0-9]|1[0-9][0-9]|200)$/,
    message: '周报-鳕鱼胶原蛋白肽饮品销售数量必须为>=0且<=200的整数'
  },
  weekendStockAmt: {
    alias: '周末库存金额',
    required: true,
    validate (val) {
      return val >= 10000 || '周报-周末库存金额必须>=10000'
    }
  },
  lijiaFulltimeConsutantNo: {
    alias: '在职理家顾问数',
    required: true,
    pattern: /^([0-9])$/,
    message: '周报-在职理家顾问数必须为>=0且<10的整数'
  },
  returnToWorkConsutantNo: {
    alias: '返岗理家顾问数',
    required: true,
    pattern: /^([0-9])$/,
    message: '周报-返岗理家顾问数必须为>=0且<10的整数'
  },
  managerNo: {
    alias: '店长数量',
    required: true
  }
}

export const monthlyRules = {
  decorationCharge: {
    alias: '装修总费用',
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-装修总费用必须>=0'
  },
  laborCharge: {
    alias: '人工支出',
    required: true,
    validate (val, target) {
      return (val > 0 && val < 100000) || '月报-人工支出必须大于0小于10万'
    }
  },
  roomCharge: {
    alias: '房租',
    required: true,
    validate (val, target) {
      return (val > 0 && val < 50000) || '月报-房租必须大于0小于5万'
    }
  },
  lightWaterCharge: {
    alias: '水电费',
    required: true,
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-水电费必须>=0'
  },
  promoCharge: {
    alias: '促销费',
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-促销费必须>=0'
  },
  phoneCharge: {
    alias: '电话费',
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-电话费必须>=0'
  },
  logisticalCharge: {
    alias: '运输费',
    required: true,
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-运输费必须>=0'
  },
  taxCharge: {
    alias: '税费',
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-税费必须>=0'
  },
  othersCharge: {
    alias: '其他费用',
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-其他费用必须>=0'
  },
  /* psmCharge: {
    alias: 'PSM费用',
    required: true,
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-PSM费用必须>=0'
  },
  warehouseCharge: {
    alias: '仓库费用',
    required: true,
    pattern: /^[+]?\d+(.\d+)?$/,
    message: '月报-仓库费用必须>=0'
  }, */
  wechatGrpNo: {
    alias: '建立微信群数量',
    pattern: /^[+]?\d+$/,
    message: '月报-建立微信群数量必须为>=0的整数'
  },
  wechatMembersNo: {
    alias: '加入微信群会员数',
    pattern: /^[+]?\d+$/,
    message: '月报-加入微信群会员数必须为>=0的整数'
  },
  wechatMomentsMembersNo: {
    alias: '加入朋友圈会员数',
    pattern: /^[+]?\d+$/,
    message: '月报-加入朋友圈会员数必须为>=0的整数'
  }
}



